@use "font" as *;
@use "button" as *;
@use "icon" as *;

.export-list {
	display: flex;
	flex-direction: column;
	@extend %font-3;

	&__item {
		padding-top: 8px;

		&:not(:last-child) {
			padding-bottom: 8px;
			border-bottom: 1px solid var(--default-border-color);
		}

		&__name {}

		&__value {
			padding-left: 12px;
			display: flex;
			flex-direction: column;

			a {
				@extend %button-text;

				&::before {
					content: "";
					@extend %icon, %icon--download;
				}
			}
		}
	}
}