@use "font" as *;
@use "button" as *;
@use "icon" as *;
@use "breakpoint" as *;
@use "mixins" as *;

.controls {
	display: flex;
	flex-direction: column;
	gap: gap-scale(0.75);

	@media (max-width: $mobile) {
		flex-direction: row;
		flex-wrap: wrap;
	}

	&__row {
		display: flex;
		flex-wrap: wrap;
		gap: 16px 0;

		@media (max-width: $mobile) {
			display: contents;
		}
	}

	&__group {
		display: flex;
		flex-wrap: wrap;
		gap: 16px;
		padding-left: 8px;

		@media (max-width: $mobile) {
			display: contents;
		}

		&:not(:last-child):not(:nth-last-child(2)) {
			padding-right: 8px;
			border-right: 1px solid var(--default-border-color);
		}

		&:last-child {
			margin-left: auto;
		}
	}

	&__item:has(&__item__popover) &__button {
		&::after {
			content: "";
			@extend %icon, %icon--chevron_down;
		}

		&.is-active {
			&::after {
				transform: scaleY(-1);
			}
		}
	}

	&__item {
		display: flex;
		align-items: center;
		gap: 8px;
		color: var(--color-text1);
		flex-wrap: wrap;
		position: relative;
		@extend %font-2;

		&__popover {
			position: absolute;
			top: calc(100% + 10px);
			right: 0;
			background-color: #fff;
			z-index: 1;
			padding: 12px;
			box-shadow: 0 5px 12px color-mix(in srgb, var(--color-text) 25%, #0000);
			border-radius: var(--default-border-radius-small);

			&:not(.is-active) {
				opacity: 0;
				pointer-events: none;
			}
		}

		em {
			font-weight: 700;
		}
	}

	&__button {
		@extend %button-text;

		&__collapse {
			&::after {
				content: "";
				@extend %icon, %icon--expand;
			}

			&.is-active {
				@extend %icon, %icon--collapse;
			}
		}
	}
}